      <img v-if="sindex == 2" src="../../assets/qtwo.jpg" />
      <img v-if="sindex == 3" src="../../assets/qthree.jpg" />
      <img v-if="sindex == 4" src="../../assets/qfour.jpg" />
      <img v-if="sindex == 5" src="../../assets/qfive.jpg" />
      <img v-if="sindex == 6" src="../../assets/qsix.jpg" />
      <img v-if="sindex == 7" src="../../assets/qsenven.jpg" />
      <img v-if="sindex == 8" src="../../assets/qeight.jpg" />
      <img v-if="sindex == 9" src="../../assets/qnine.jpg" />
      <img v-if="sindex == 10" src="../../assets/qten.jpg" />

    </div>
    <Music />
    <div class="home_container">
