        <!-- <div class="line"></div> -->
      </div>
      <div class="title">上传游戏信息</div>
      <div class="home_form">
        <van-field
          v-model="userForm.gameId"
          label="游戏ID:"
          readonly
          @focus="userId"
        />
        <van-field
          v-model="userForm.gameValue"
          label="游戏段位:"
