          <img v-else src="./../../assets/yxsl.png"  alt="" />
        </div>
        <van-uploader :after-read="afterRead">
          <a class="updatabtn">上传游戏截图</a>
        </van-uploader>
        <Vue-imageCutting
          v-if="picloalog"
          :picturePath="fileContent"
          :crossDomain="crossDomainData"
          @confirm="confirmMethod"
          @cancel="cancelMethod"
          @rotate="rotateMethod"
          @restore="restoreMethod"
          @loading="loadingMethod"
          @touching="touchingMethod"
        />
      </div>

      <div class="tix">
        <p>每位ID限量只能领取一次奖励！重复领取无效</p>
      </div>
      <div class="back" style="margin-bottom: 0.2rem">
        <div @click="truebtn">确认上传</div>
      </div>
      <div class="up">
        <van-popup v-model="dialogValue">
          <div class="dialog_title">
            <span>「上传提示」</span>
          </div>
          <div class="word">
            <div class="word_left">
              <p>请到活动现场处确认</p>
              <p>凭借上传成功截图方可领奖</p>
              <p>否则无效</p>
            </div>
          </div>
          <div class="dialog_btn">
            <div class="dialog_back">
              <div @click="hcback">取消</div>
              <!-- <div @click="a">取消</div> -->
            </div>
            <div class="dialog_back">
              <div @click="goto">确定</div>
            </div>
          </div>
        </van-popup>
      </div>

      <div class="uploadSuccess">
        <van-popup v-model="uploadSuccessVisible">
          <div class="successContent">
            <div class="id">您的游戏ID：{{ userForm.gameId || "无" }}</div>
            <div class="v">您的游戏段位：{{ userForm.gameValue || "无" }}</div>
            <div class="img" @click="BigImg">
              <img style="object-fit: cover;" src="../../assets/qone.jpg" />
            </div>
            <div class="tip">请长按宣传海报保存至相册，发送朋友圈集赞！</div>
            <div class="dialog_back">
              <div @click="uploadSuccessVisible = false">确定</div>
            </div>
          </div>
        </van-popup>
      </div>

      <van-loading v-if="!loadingData" type="spinner" size="24px"
        >加载中...</van-loading
      >
    </div>
   
  </div>
</template>

<script>
import {
  GetGameupdata,
  getAliOSSConfig,
  GetGameData,
  getJsConfig,
} from "@/api/api";
import axios from "axios";
import Music from "@/components/Music/index.vue";
import { mapState } from "vuex";
export default {
  components: { Music },
  data() {
    return {
      picturePathData: "xxx.jpg",
      crossDomainData: false,
      show: false,
      actions: [
        { name: "最强王者" },
        { name: "无双王者" },
        { name: "荣耀王者" },
      ],
      userForm: {
        gameId: "",
        gameValue: "",
      },
      getInfo: {
        id: "",
        val: "",
      },
      uploadSuccessVisible: false,
      dialogValue: false,
      updataimg: {
        //图片base
        cont: "",
        // 图片名字
        name: "",
        //图片地址
        lastModified: "",
      },
      picdata: {
        recognize_id: "",
        recognize_level: "",
        img_uri: "",
      },
      // base64
      fileContent: "",
      // 文件
      file: null,
      // 是否加载完
      loadingData: true,
      // 加载完成显示图
      loadimg: false,
      option1: [
        { text: "全部商品", value: 0 },
        { text: "新款商品", value: 1 },
        { text: "活动商品", value: 2 },
      ],
      //裁剪弹框
      picloalog: false,
      scanData: "",
      // 图片放大缩小
      PicVal: false,
      sindex:''
    };
  },
  computed: {
    ...mapState(["schoolindex"]),
  },
  mounted() {
    this.init();
  },
  methods: {
    SomePic() {
      this.PicVal = false;
    },
    // 放大图片
    BigImg() {
      console.log(this.schoolindex);
      this.sindex = this.schoolindex
      this.PicVal = true;
    },
    userDw(e) {
      this.Toast("无需输入，请先上传图片");
    },
    userId(e) {
      this.Toast("无需输入，请先上传图片");
    },
    init() {
      getJsConfig({
        url: "",
      }).then((res) => {
        console.log(res);
        if (res.status === 200) {
          let config = res.data || {};
          jWeixin.config({
            debug: false,
            appId: config.appId,
            timestamp: config.timestamp,
            nonceStr: config.nonceStr,
            signature: config.signature,
            jsApiList: [],
          });
          jWeixin.ready(() => {
            jWeixin.hideMenuItems({
              menuList: [
                "menuItem:copyUrl",
                "menuItem:share:appMessage",
                "menuItem:share:timeline",
                "menuItem:share:qq",
                "menuItem:share:weiboApp",
                "menuItem:share:facebook",
                "menuItem:share:QZone",
              ],
            });

            jWeixin.hideOptionMenu();
          });
        } else {
          this.handleError(res);
        }
      });
    },
    touchingMethod(event) {
      // console.log(event);
    },
    restoreMethod(event) {
      console.log(event);
    },
    rotateMethod(event) {
      console.log(event);
    },
    cancelMethod(event) {
      console.log(event);
      this.picloalog = false;
    },
    loadingMethod(event) {
      console.log(event);
    },
    confirmMethod(event) {
      // console.log(event);
      // console.log(this.scanData);
      this.picloalog = false;
      this.scanData = event.base64;
      this.getImageInfo();
    },
    cutDown(event) {
      console.log(event);
    },
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      // this.Toast(item.name);
      this.userForm.gameValue = item.name;
    },
    hcback() {
      this.dialogValue = false;
    },
    truebtn() {
      this.dialogValue = true;
    },
    // 返回
    back() {
      this.$router.push("/pagefive");
    },
    //上传触发事件
    afterRead(file) {
      console.log(file);
      this.file = file;
      // this.updataimg = file.content
      this.updataimg.lastModified = file.file.type;
      this.updataimg.cont = file.content;
      this.fileContent = file.content;
      this.picloalog = true;
    },
    confirm() {
      this.dialogValue = false;
    },
    async getImageInfo() {
      this.getInfo.val = "";
      this.getInfo.id = "";
      this.loadingData = false;
      let key = "wUbxngjGDBUEsS-kulRcXh2Jp-9dXkVM";
      let secret = "gxcRJD0wqwVElf2OemeFMLWqLIo9ywNR";
      let image_base64 = this.scanData;
      let formData = new FormData();
      formData.append("api_key", key);
      formData.append("api_secret", secret);
      formData.append("image_base64", image_base64);
      axios({
        url: "https://api-cn.faceplusplus.com/imagepp/v2/generalocr",
        method: "post",
        data: formData,
      }).then((res) => {
        // console.log(res);
        this.loadingData = true;
        if (res.status == 200) {
          // this.loadimg = this.updataimg.cont;
          // this.loadingData = true;
          // let data = res.data.text_info;
          // console.log(data);
          // this.scanData = image_base64;
          // this.picloalog = true;
          // let index = data[data.length - 4].line_content.indexOf("我的排名");
          // console.log(index);
          // if (index == 0) {
          //   console.log(data[data.length - 4].line_content);
          //   console.log(data[data.length - 1].line_content);
          //   let dw = data[data.length - 1].line_content;
          //   var name = dw;
          //   var reg = /[\u4e00-\u9fa5]/g;
          //   var names = name.match(reg);
          //   name = names.join("");
          //   console.log(name);
          //   //游戏段位（识别）
          //   this.picdata.recognize_level = name;
          //   //游戏id（识别）
          //   this.picdata.recognize_id = res.data.request_id;
          // }

          let textInfo = res.data.text_info;
          // 获取我的排名索引
          let mIndex = textInfo.findIndex((v) => {
            return v.line_content === "我的排名";
          });
          if (mIndex < 0) {
            // 无我的排名字样，提示，中止
            this.Toast("请截图包含我的排名部分");
            return;
          }
          let name = textInfo[mIndex + 1] || {};
          this.userForm.gameId = name.line_content || "未获取到";
          this.getInfo.id = name.line_content || "未获取到";
          let dwStrs = ["至尊星耀", "最强王者", "无双王者", "荣耀王者"]; //this.actions.map((v) => v.name)
          let val = "";
          for (let v of textInfo.slice(mIndex)) {
            let text = v.line_content;
            let tindex = dwStrs.findIndex((t) => text.includes(t));
            if (tindex >= 0) {
              val = dwStrs[tindex];
            }
          }
          if (!val) {
            this.Toast(
              "很遗憾 您的段位没达到活动要求，邀请小伙伴们一起上分吧！"
            );
            return;
          }
          this.userForm.gameValue = val;
          this.getInfo.val = val;
        } else {
          this.Toast("获取截图信息失败，请重试");
        }
      });
    },
    //上传事件
    async goto() {
      this.loadingData = false;
      if (!this.getInfo.val) {
        this.Toast("很遗憾 您的段位没达到活动要求，邀请小伙伴们一起上分吧！");
        this.loadingData = true;
        return;
      }
      if (this.userForm.gameId == "") {
        this.Toast("请输入游戏ID");
        this.loadingData = true;
        return;
      }
      if (this.userForm.gameValue == "") {
        this.Toast("请输入段位");
        this.loadingData = true;
        return;
      }
      if (this.getInfo.val !== this.userForm.gameValue) {
        this.Toast("请选择与上传图片一致的游戏段位！");
        this.loadingData = true;
        return;
      }
      // if (this.picdata.recognize_id == "") {
      //   this.Toast("请先上传图片");
      //   this.loadingData = true;
      //   return;
      // }
      // 此时可以自行将文件上传至服务器
      this.loadingData = false;
      let res = await getAliOSSConfig();
      let OSSConfig = res.data.data;
      console.log(OSSConfig);
      const form = new FormData();
      // const filename = this.file.file.name;
      const filename = new Date().getTime() + ".jpg";
      form.append("name", `${filename}`);
      form.append("key", OSSConfig.dir + filename);
      form.append("policy", OSSConfig.policy);
      form.append("OSSAccessKeyId", OSSConfig.accessid);
      // form.append("success_action_status", 200);
      form.append("callback", OSSConfig.callback);
      form.append("signature", OSSConfig.signature);
      form.append("file", this.file.file);
      console.log(this.file);
      const result = await GetGameupdata(OSSConfig.host, form).then((res) => {
        console.log(res);
        if (res.data.status == 200) {
          this.picdata.img_uri = res.data.data.filename;
        }
      });
      console.log(this.picdata.img_uri);
      GetGameData({
        game_id: this.userForm.gameId,
        game_level: this.userForm.gameValue,
        recognize_id: this.getInfo.id,
        recognize_level: this.getInfo.val,
        img_uri: this.picdata.img_uri,
      }).then((res) => {
        console.log(res);
        this.loadingData = true;

        if (res.data.status == 200) {
          this.dialogValue = false;
          this.uploadSuccessVisible = true;
        } else {
          this.Toast(res.data.msg);
        }
      });
    },
  },
};
</script>

